.about-container {
    max-width: 1183px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    margin-top: 50px;
}

.about-container h1 {
    font-size: 32px;
    color: #333;
    margin-bottom: 20px;
}

.about-container p {
    font-size: 18px;
    color: #555;
    line-height: 1.6;
    margin-bottom: 16px;
}